<template>
	<div class='exam-view'>
		<button @click='$router.push("/马克思/exam/paper")' v-show='$route.path==="/马克思/exam"'>开始考试</button>
		<router-view></router-view>
	</div>
</template>

<script>
	import {getText} from '../../../axios/api.js'
	import book from './store.js'
	export default {
		data() {
			return {
			};
		},
		beforeCreate(){
			//从题库拉取题
			getText(1, 0).then(
				(res)=>{
					book.on = 0;
					for(let i = 0; i < 100; i ++){
						book.text[i] = res.data[i+10];
						book.answer[i] = 0;
					}
					// console.log(book.text)
				}
			)
		}
	}
</script>

<style>
	.exam-view{
		height: 100%;
	}
</style>



