<template>
	<div class='answerSheet'>
		<div>
			<div
				:class="{'circle':true, 'blank':(a[index]===0)}"
				v-for='(item, index) in k'
				@click='gotoPaper(index)'
			>
				{{index+1}}
			</div>
		</div>
		<div class='submit' @click='submitPaper()'>交卷</div>
	</div>
</template>

<script>
	import book from './store.js'
	export default {
		data() {
			return {
				a: book.answer,
				k: book.text
			};
		},
		methods:{
			gotoPaper(num){
				book.on = num;
				this.$router.push("/马克思/exam/paper")
			},
			submitPaper(){
				this.$router.push("/马克思/exam/grade")
			}
		},
		mounted(){
			this.a = book.answer;
			console.log(this.a)
		}
	}
</script>

<style lang='scss'>
	@import "../../../style/mixin";
	@import "../../../style/style";
	.answerSheet{
		padding: 1rem;
		box-sizing: border-box;
		.circle{
			height: 2.5rem;
			width: 2.5rem;
			display: inline-block;
			border-radius: 100%;
			background-color: #FFFFFF;
			color:#A4845D;
			border: #A07A4C 2px solid;
			line-height: 2.5rem;
			text-align: center;
			margin: .2rem;
			font-size: .8em;
		}
		.blank{
			background-color: #E3E3E3;
		}
		.submit{
			@include card(100%, 4rem, 1rem);
			margin-top: .4rem;
		}
	}
</style>



